.card-wrapper {
    /* background: transparent; */
    background: url(/static/media/red-dots.c34322cb.svg);
    background-repeat: no-repeat;
    background-size: cover;
    width: 90%;
    margin: 0 auto;
    margin-top: 4em;
    cursor: pointer;
    -webkit-transition: cubic-bezier(0.075, 0.82, 0.165, 1) 800ms;
    transition: cubic-bezier(0.075, 0.82, 0.165, 1) 800ms;
    /* background-position: center; */
}
.card-wrapper:hover {
    background-position: center;
    /* background-color: #fff; */
    /* width: 95%; */
}
.card-container {
    background-color: #801B1B;
    /* background-color: red; */
    color: #fff;
    padding: .5rem 1.5rem 2rem 2rem;
    opacity: .7;
    position: relative;
}
.card-container:hover {
    background-color: #fff;
    color: #801B1B;
    fill: #801B1B;
    -webkit-transition: cubic-bezier(0.075, 0.82, 0.165, 1) 300ms;
    transition: cubic-bezier(0.075, 0.82, 0.165, 1) 300ms;
    /* padding: .7rem 1.7rem 2.2rem 2.2rem; */
    /* height: ; */
    /* padding: .7rem 1.5rem 2.2rem 2rem; */
    /* filter: invert(100%) sepia(0%) saturate(1%) hue-rotate(250deg) brightness(104%) contrast(101%); */
    opacity: 0.8;
}
