.sponsors-wrapper {
    padding: 1rem;
}
.sponsors-wrapper h1 {
    color: #fff;
    text-align: center;
    font-size: 2.5em;
}
.sponsors-wrapper p{
    color: rgb(228, 216, 216);
    font-weight: 600;
}
.spon-heading{
    text-align: center;
    padding-top: 10px;
    padding-bottom: 10px;
    color: #272C2E;
    font-size: 1.2rem;
    letter-spacing: 1px;
    /* font-weight: 500 !important;  give font weight to direct h1*/
}

@media only screen and (max-width: 600px){
    .pepsi-logo{
        height: auto;
        width: 90px;
        
    }
    .oneplus-logo{
        height: auto;
        width: 130px;
        position: relative;
        top: 50px;
        filter: grayscale(100%);
        -webkit-filter: grayscale(100%);
        /* opacity: .6; */
        -webkit-transition: .6s;
        -moz-transition: .6s;
        transition: .6s;
    }
}


.pepsi-logo:hover, .oneplus-logo:hover {
    filter: grayscale(0%);
    opacity: 1;
    /* margin-top: -5px; */
    /* box-shadow: 0px 18px 22px -15px rgba(0,0,0,0.3); */
}
@media only screen and (min-width: 600px){
    .pepsi-logo{
        width: 130px;
        /* filter: grayscale(100%);
        -webkit-filter: grayscale(100%);
        opacity: .6;
        -webkit-transition: .6s;
        -moz-transition: .6s;
        transition: .6s; */
    }
    .oneplus-logo{
        width: 90px;
        top: 30px;
    }
}
.spon-btn{
    background-color: #272C2E;
    color: white;
}
.spon-contact{
    text-align: center;
    color: #fff;
}